<template>
  <div>
    <mynav></mynav>
    <h5 class="describe"><p>所有通知</p></h5>
    <div class="list">
      <div class="option flex-col" v-for="item in notices">
        <div class="flex-row line1">
          <p class="flex-row">
            <span v-if="item.fromimg.length < 10" class="avatar-bg"></span>
            <img v-else :src="'http://' + item.fromimg" :alt="item.fromname" class="avatar">
            <span>来自 <span v-text="item.fromname"></span></span>
          </p>
          <div class="date">{{ item.createtime | formatDate }}</div>
        </div>
        <div class="flex-row line2">
          <span v-text="item.content"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from '../api.js'
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'

  export default {
    name: 'Notices',
    components: {
      Mynav,
      MButton
    },
    data () {
      return {
        notices: []
      }
    },
    mounted () {
      this.getNotices()
    },
    methods: {
      getNotices () {
        api.GetNotices({userid: this.$root.userid}).then(res => {
          res.map(n => this.notices.push(n))
        }).catch(error => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>
  .describe {
    margin: 24px 0;
    color: #999;
    font-size: 18px;
    font-weight: 600;
    /*font-family: "宋体";*/
  }

  .describe p {
    width: 650px;
    margin: 0 auto;
    line-height: 1;
  }

  .list {
    width: 630px;
    margin: 20px auto;
    background: #fff;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, .1);
    padding: 10px;
  }

  .list .option {
    justify-content: space-between;
    padding: 8px 20px;
    border-bottom: 1px solid #eee;
    letter-spacing: 1px;
  }

  .list .line1 {
    justify-content: space-between;
    line-height: 32px;
    padding: 8px 0;
    align-items: center;
  }

  .line1 .avatar {
    display: inline-block;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    margin-right: 10px;
  }

  .line1 .avatar-bg {
    display: inline-block;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    margin-right: 10px;
    background: #38bff3;
  }

  .list .line2 {
    line-height: 36px;
    padding-left: 42px;
  }

  .date {
    color: #777;
    font-size: 12px;
  }
</style>


