<template>
  <div id="header">
    <!--<slidenav :visible="slidenavState" v-on:close="closeNav"></slidenav>-->
    <div class="left">
      <div class="flex-row brand" style="width: 300px;">
        <img src="../assets/logo-white.png" alt="">
        <router-link to="/home" tag="span">MedNotes</router-link>
      </div>
    </div>
    <div class="right">
      <router-link to="/notice" tag="i" class="iconfont icon-tongzhi"></router-link>
      <i class="iconfont icon-menu" @click.stop="changeCloud"></i>
      <span class="avatar" @click.stop="showConfig" v-if="$root.userInfo.s_avatar === ''">{{ $root.shortName.substring(0, 3) | uppercase }}</span>
      <span class="avatar" @click.stop="showConfig" v-else><img :src="'http://' + $root.userInfo.s_avatar" alt="" class="avatar-img"></span>
      <!--<span class="avatar" @click.stop="showConfig">{{ $root.shortName | uppercase }}</span>-->
    </div>

    <popover ref="config" class="config flex-col">
      <div id="user-info" class="flex-row">
        <span class="avatar large">{{ $root.shortName.substring(0, 3) | uppercase }}</span>
        <div class="text flex-col" style="flex: 1;">
          <span class="name">{{ $root.userInfo.s_username }}</span>
          <span class="work">{{ $root.userInfo.s_workunti }}</span>
        </div>
      </div>
      <div class="link-list flex-col">
        <router-link to="/profile" class="link-item">用户设置</router-link>
        <router-link to="/profile" class="link-item">用户帮助</router-link>
      </div>
      <div class="exit"><m-button type="blue" @click="exit">退出</m-button></div>
    </popover>
    <popover ref="outsite" class="outsite">
      <div class="flex-col" @click="go('mobilehome')">
        <i class="iconfont icon-beizhu" style="font-size: 42px;color: orange;"></i>
        <span style="font-size: 13px;text-align: center;">MedNotes</span>
      </div>
      <div class="flex-col">
        <i class="iconfont icon-xiangmu" style="font-size: 38px;color: #dadada;"></i>
        <span style="font-size: 13px;text-align: center;color: #aaa;">云管理</span>
      </div>
      <div class="flex-col">
        <i class="iconfont icon-xiangmu1" style="font-size: 39px;color: #dadada;"></i>
        <span style="font-size: 13px;text-align: center;color: #aaa;">云影像</span>
      </div>
    </popover>

  </div>
</template>

<script>
  import API from '../api.js'
  // import Slidenav from './SlideNav.vue'
  import Popover from '../lib/Popover.vue'
  import MButton from '../lib/Button.vue'

  export default {
    name: 'Header',
    components: {
      MButton,
      Popover
    },
    data () {
      return {
        slidenavState: false
      }
    },
    created () {
      // 只有用户信息为空的时候请求（解决刷新，页面切换重复请求问题）
      if (Object.keys(this.$root.userInfo).length === 0) {
        this.getUserInfo()
      }
    },
    methods: {
      getUserInfo () {
        API.GetUserinfo({userid: this.$root.userid}).then(response => {
          this.nameToshort(response[0].s_username)
          this.$root.userInfo = response[0]
        }).catch(err => {
          console.log(err)
        })
      },
      nameToshort (str) {
        API.ChineseToShort({string: str}).then(response => {
          this.$root.shortName = response.shortpinyin
        }).catch(err => {
          console.log(err)
        })
      },
      changeNav () {
        this.slidenavState ? this.slidenavState = false : this.slidenavState = true
      },
      closeNav () {
        this.slidenavState = false
      },
      showConfig (event) {
        this.$refs.config.show(event.target)
        this.$refs.outsite.hide()
      },
      changeCloud (event) {
        this.$refs.outsite.show(event.target)
        this.$refs.config.hide()
      },
      go (siteAddress) {
        window.location.href = '/user/' + siteAddress
      },
      exit () {
        this.$root.userid = ''
        this.$root.userInfo = {}
        this.$root.shortName = ''
        this.$root.currentProject = {}
        window.sessionStorage.clear()
        this.$router.push({path: '/login'})
      }
    }
  }
</script>

<style scoped>
 #header {
   display: flex;
   justify-content: space-between;
   align-items: center;
   font-size: 18px;
   color: #fff;
   height: 64px;
   background: #468df1;
   /*border-bottom: 1px solid #e1e4e8;*/
   padding: 0 32px;
 }

 .brand img{
  width: 130px;
  height: 44px;
  margin-right: 20px;
 }

 .brand span {
   display: inline-block;
   width: 100px;
   font-size: 23px;
   padding-left: 20px;
   border-left: 1px solid #ddd;
   font-family: "Roboto Mono",Roboto,sans-serif;
   letter-spacing: 1px;
   font-weight: 400;
   cursor: pointer;
   line-height: 44px;
 }

 #header .left {
   display: flex;
   align-items: center;
 }


 #header .right {
   height: 32px;
   display: flex;
   align-items: center;
 }

 #header .right i.iconfont {
   padding: 10px 12px;
   font-weight: 600;
   cursor: pointer;
 }

 #header .right i.iconfont:hover {
   background: rgba(255, 255, 255, .2);
   border-radius: 50%;
 }

 .avatar {
   display: inline-block;
   height: 32px;
   width: 32px;
   font-size: 12px;
   text-align: center;
   line-height: 32px;
   color: #222;
   background: #fff;
   border-radius: 50%;
   margin-left: 12px;
   cursor: pointer;
   overflow: hidden;
 }

 .avatar-img {
   display: inline-block;
   height: 100%;
   width: 100%;
 }

 .config {
   width: 288px;
   background: #fff;
 }

 .config #user-info {
   height: 102px;
   display: flex;
   align-items: center;
   padding: 0 24px;
   overflow-x: hidden;
   border-bottom: 1px solid #eee;
   background: #fafafa;
 }

 .config #user-info .avatar {
   height: 70px;
   width: 70px;
   background: #468df1;
   color: #fff;
   line-height: 70px;
   font-size: 22px;
   margin-left: 0;
   margin-right: 12px;
 }

 .config #user-info .name {
   font-size: 18px;
   line-height: 24px;
 }

 .config #user-info .work {
   font-size: 14px;
   line-height: 24px;
 }
 .config .link-list {
   font-size: 18px;
   line-height: 24px;
   padding: 12px;
   background: #fff;
   margin: 0 12px;
   border-bottom: 1px solid #eee;
 }

 .config .link-list a{
   font-size: 18px;
   line-height: 2;
 }

 .config .exit {
   background: #fff;
   text-align: right;
   padding: 12px 12px 12px 0;
 }

 .config .exit .button {
   font-size: 18px;
   background: #fff;
   color: #468df1;
   border: 1px solid #468df1;
 }

 .config .exit .button:hover {
   background: #468df1;
   color: #fff;
 }
 
 #header .outsite{
   padding: 28px;
   width: 320px;
   display: flex;
 }

 .outsite>div {
   flex: 1;
   justify-content: center;
   align-items: center;
   cursor: pointer;
 }

 .outsite i {
   display: inline-block;
   /*height: 64px;*/
   /*width: 64px;*/
   line-height: 64px;
 }
</style>


