<template>
  <div>
    <mynav></mynav>
    <h2 class="header-state">
      <span :class="{active: !public}" @click="public = false">我的项目</span>
      <span :class="{active: public}" @click="changeView">所有公开项目</span>
    </h2>

    <h5 class="describe" v-if="!public"><p>我的项目</p></h5>
    <div class="flex-row cards projects" v-if="!public">
      <div class="card bg-blue" v-for="p in projects">
        <router-link :to="'/project/' + p.u_projectID + '/patients/1'">
          <div class="color" :class="{project1: p.u_projectID === '1', project2: p.u_projectID === '2'}"></div>
          <div class="content">
            <div class="name">{{ p.u_projectName }}</div>
            <div class="sub">{{ p.u_projectMem }}</div>
          </div>
        </router-link>
      </div>
    </div>

    <h5 class="describe" v-if="public"><p>所有公开项目</p></h5>
    <div class="flex-row cards projects-public" v-if="public">
      <div class="card bg-blue" v-for="p in publicList">
        <div class="color" :class="{project1: p.u_projectID === '1', project2: p.u_projectID === '2'}"></div>
        <div class="content">
            <div class="name">{{ p.u_projectName }}</div>
            <div class="sub">{{ p.u_projectMem }}</div>
            <span v-if="p.is_join">已加入</span>
            <span class="join-btn" v-if="!p.is_join" @click="joinTeam(p.u_projectID)">申请加入</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import API from '../api.js'
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'
  import Datepicker from '../lib/Date.vue'

  export default {
    name: 'Home',
    components: {
      Mynav,
      MButton,
      Datepicker
    },
    data () {
      return {
        public: false,
        publicList: [],
        projects: []
      }
    },
    mounted () {
      // 初始参与项目 List
      this.getProjects()
    },
    methods: {
      changeView () {
        this.public = true
        this.getAllProjects()
      },
      // 获取公开项目
      getAllProjects () {
        this.publicList = []
        API.GetAllProjects().then(res => {
          res.map(n => {
            this.publicList.push({
              u_projectMem: n.u_projectMem,
              u_projectID: n.u_projectID,
              u_projectName: n.u_projectName,
              is_join: false
            })
          })
          this.checkPublic()
        }).catch(error => {
          console.log(error)
        })
      },
      // 已申请判断
      checkPublic () {
        for (let id in this.publicList) {
          // console.log(this.projects[id].u_projectID + '===' + n.u_projectID)
          for (let myid in this.projects) {
            this.projects[myid].u_projectID === this.publicList[id].u_projectID ? this.publicList[id].is_join = true : ''
          }
        }
      },
      // 申请加入项目
      joinTeam (id) {
        console.log(id)
        API.ApplyProject({userid: this.$root.userid, projectid: id}).then(res => {
          this.toast({
            type: 'success',
            text: '申请成功',
            placement: 'bottom-left'
          })
        }).catch(error => {
          this.toast({
            type: 'error',
            text: '申请失败，请重新尝试',
            placement: 'bottom-left'
          })
          console.log(error)
        })
      },
      // 获取我参与的项目
      getProjects () {
        API.GetProjects({userid: this.$root.userid}).then(res => {
          res.map(n => this.projects.push(n))
        }).catch(error => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>
  .header-state {
    background: #468df1;
    height: 48px;
    color: rgba(255, 255, 255, .8);
    font-size: 16px;
    letter-spacing: 1px;
    text-align: center;
    line-height: 48px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.23);
  }

  .header-state span {
    cursor: pointer;
    display: inline-block;
    height: 45px;
    width: 180px;
  }

  .header-state span.active {
    color: #fff;
    border-bottom: 3px solid #fff;
  }

  .describe {
    margin: 24px auto;
    color: #999;
    width: 768px;
    font-size: 18px;
    font-weight: 600;
    /*font-family: "宋体";*/
  }

  .describe p {
    padding-left: 6px;
    line-height: 1;
  }

  .projects, .projects-public {
    margin: 24px auto;
    width: 768px;
    flex-wrap: wrap;
    /*justify-content: space-around;*/
  }

  .card {
    background: #fff;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.2), 0 1px 5px 0 rgba(0,0,0,0.12);
    line-height: 32px;
    width: 244px;
    height: 254px;
    margin: 8px 6px;
  }

  .card .color {
    height: 136px;
    background-size: cover;
    background-position: center;
    background-color: #9e9e9e;
  }

  .card .color.project1 {
    background-image: url('../assets/project1.jpg');
  }

  .card .color.project2 {
    background-image: url('../assets/project2.jpg');
  }


  .card .content {
    margin-top: 12px;
    font-size: 14px;
    padding-left: 12px;
    color: rgba(0, 0, 0, 0.8);
  }

  .card .content .name {
    font-size: 16px;
    font-weight: 600;
    color: #222;
  }

  .join-btn {
    color: #468df1;
  }

</style>


